                                            :data-parentid="child.AssetCatalogueId" :data-uniqueid="child.ChildUniqueId"
                                            :data-id="child.ChildUniqueId" :data-availableitems="child.AvailableItems"
                                            data-requestquantity="" data-batchid="" data-request_from_requisition="1"
