                                                :value="Boolean(child.CreateUniqueNameListing) == true ? 1 : child.AsseetGroupQuantity" />
                                            <input v-else :data-requestquantity="child.AvailableItems"
                                                :data-availableitems="child.AvailableItems"
                                                class="txtitems textfield text-right form-control align-middle " type="text"
                                                :value="Boolean(child.CreateUniqueNameListing) == true ? 1 : child.AsseetGroupQuantity" />
